import { fetchEndpoint } from '../../network/network';

// 
export const authSignin =
(username, password) =>
fetchEndpoint(
  '/v2/ecapi.auth.signin',
  'POST',
  {
    "username": username,  // //email
    "password": password,  // 
  }
)




