import { fetchEndpoint } from '../../network/network';

// 
export const authSocial =
(vendor, access_token, open_id) =>
fetchEndpoint(
  '/v2/ecapi.auth.social',
  'POST',
  {
    "vendor": vendor,  // 
    "access_token": access_token,  // OAUTH TOKEN
    "open_id": open_id,  // ID
  }
)




