import { fetchEndpoint } from '../../network/network'
// 
// export const orderPrice = (params) => fetchEndpoint('/v2/ecapi.order.price', 'POST', params);

// 
export const orderGet =
(order) =>
fetchEndpoint(
  '/v2/ecapi.order.get',
  'POST',
  {
    "order": order,  // ID
  }
)

// 
export const orderList =
(page, per_page, status) =>
fetchEndpoint(
  '/v2/ecapi.order.list',
  'POST',
  {
    "page": page,  // 
    "per_page": per_page,  // 
    "status": status,  // 
  }
)

// 
export const orderConfirm =
(order) =>
fetchEndpoint(
  '/v2/ecapi.order.confirm',
  'POST',
  {
    "order": order,  // ID
  }
)

// 
export const orderReasonList =
() =>
fetchEndpoint(
  '/v2/ecapi.order.reason.list',
  'POST',
  {
  }
)

// 
export const orderCancel =
(order, reason) =>
fetchEndpoint(
  '/v2/ecapi.order.cancel',
  'POST',
  {
    "order": order,
    "reason": reason,  // 
  }
)

// 
export const orderReview =
(order, review, is_anonymous) =>
fetchEndpoint(
  '/v2/ecapi.order.review',
  'POST',
  {
    "order": order,  // id
    "review": review,  // 
    "is_anonymous": is_anonymous,  // 10
  }
)

// 
export const orderSubtotal =
() =>
fetchEndpoint(
  '/v2/ecapi.order.subtotal',
  'POST',
  {
  }
)

// 
export const orderPrice =
(shop, order_product, consignee, shipping, coupon, cashgift, score) =>
fetchEndpoint(
  '/v2/ecapi.order.price',
  'POST',
  {
    "shop": shop,  // ID
    "order_product": order_product,  // 
    "consignee": consignee,  // ID
    "shipping": shipping,  // ID
    "coupon": coupon,  // ID
    "cashgift": cashgift,  // ID
    "score": score,  // 
  }
)

//   1.1.0     
export const orderRebuy =
(order) =>
fetchEndpoint(
  '/v2/ecapi.order.rebuy',
  'POST',
  {
    "order": order,  // ID
  }
)

// 
export const orderDeliveryTimeList =
() =>
fetchEndpoint(
  '/v2/ecapi.order.delivery.time.list',
  'POST',
  {
  }
)



