import { fetchEndpoint } from '../../network/network';

// 
export const articleList =
(id, page, per_page) =>
fetchEndpoint(
  '/v2/ecapi.article.list',
  'POST',
  {
    "id": id,  // ID
    "page": page,  // 
    "per_page": per_page,  // 
  }
)




