import wx from 'weixin-js-sdk'
import { configGet } from '../api/network/config'
const wxApi = {
	/*
	* [isweixin ]
	*/
	isweixin() {
		const ua = window.navigator.userAgent.toLowerCase()
		if (ua.match(/MicroMessenger/i) == 'micromessenger') {
			return true
		} else {
			return false
		}
	},

	/*
	* getConfigRes config
	*/
	getConfigRes(title, imgUrl, desc) {
		let that = this;
		configGet().then(res => {
			if (res) {
				let configs = res.config,
					wechatConfig = {};
				for (const key in configs) {
					if (key == 'wechat.web') {
						wechatConfig = configs[key];
					}
				}
				that.wxRegister(wechatConfig, title, imgUrl, desc);
			}
		})
	},
	/**
	* [wxRegister Api]
	* @param  {Function} callback [ready]
	*/
	wxRegister(config, title, imgUrl, desc, callback) {
		wx.config({
			debug: false, // 
			appId: config.app_id, // 
			timestamp: config.timestamp, // 
			nonceStr: config.nonceStr, // 
			signature: config.signature, // 1
			jsApiList: [
				'onMenuShareTimeline', // 
				'onMenuShareAppMessage', // 
				'onMenuShareQQ', // QQ
				'onMenuShareWeibo' // 
			] // JSJS2
		});
		wx.ready(function () {
			wx.onMenuShareAppMessage({
				title: title, // 
				imgUrl: imgUrl, // 
				desc: desc // 
			});
		});
		wx.error(res => {
			console.log('error');
		})
	},

	/**
	* [ShareTimeline ]
	* @param {[type]} opstion []
	* @param {[type]} success []
	* @param {[type]} error   []
	*/
	ShareTimeline(opstion) {
		wx.onMenuShareTimeline({
			title: opstion.title, // 
			link: opstion.link, // 
			imgUrl: opstion.imgUrl, // 
			success() {
				// 
				opstion.success()
			},
			cancel() {
				// 
				opstion.error()
			}
		})
	},

	/**
	* [ShareTimeline ]
	* @param {[type]} option []
	* @param {[type]} success []
	* @param {[type]} error   []
	*/
	ShareAppMessage(option) {
		wx.onMenuShareAppMessage({
			title: option.title, // 
			desc: option.desc ? option.desc : '', // 
			link: option.link ? option.link : '', // 
			imgUrl: option.imgUrl ? option.imgUrl : '', // 
			success() {
				// 
				option.success()
			},
			cancel() {
				// 
				option.error()
			}
		})
	},

	/**
	* [ShareTimeline QQ]
	* @param {[type]} opstion []
	* @param {[type]} success []
	* @param {[type]} error   []
	*/
	ShareQQ(opstion) {
		wx.onMenuShareQQ({
			title: opstion.title, // 
			link: opstion.link, // 
			imgUrl: opstion.imgUrl, // 
			success() {
				// 
				opstion.success()
			},
			cancel() {
				// 
				opstion.error()
			}
		})
	},

	/**
	* [ShareTimeline ]
	* @param {[type]} opstion []
	* @param {[type]} success []
	* @param {[type]} error   []
	*/
	ShareWeibo(opstion) {
		wx.onMenuShareWeibo({
			title: opstion.title, // 
			link: opstion.link, // 
			imgUrl: opstion.imgUrl, // 
			success() {
				// 
				opstion.success()
			},
			cancel() {
				// 
				opstion.error()
			}
		})
	}
}

export default wxApi



