import { fetchEndpoint } from '../../network/network';

// 
export const recommendProductList =
(brand, category, shop, product, page, per_page) =>
fetchEndpoint(
  '/v2/ecapi.recommend.product.list',
  'POST',
  {
    "brand": brand,  // ID
    "category": category,  // ID
    "shop": shop,  // ID ()
    "product": product,  // ID
    "page": page,  // 
    "per_page": per_page,  // 
  }
)




